body {
  background: #00000034;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

h1 {
  text-align: center;
  color: #333;
}

.form {
  background: #00000063;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 25px;
  display: block;
  box-shadow: 10px 10px 10px 10px;
}

.form-title, .form-text {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 97%;
  margin-bottom: 10px;
  padding: 10px;
  display: block;
}

.form button {
  color: #fff;
  cursor: pointer;
  background: #000;
  border: none;
  border-radius: 5px;
  padding: 5px 25px;
  font-size: 20px;
}

.posts {
  padding: 0;
  list-style: none;
}

.posts li {
  background: #4b4949;
  border-radius: 5px;
  margin-bottom: 1rem;
  padding: 1rem;
  box-shadow: 0 0 5px #0000001a;
}

.posts h3 {
  color: #fff;
  margin-top: 0;
}

.posts p {
  color: #fff;
  margin: .5rem 0;
}

.posts button {
  color: #fff;
  cursor: pointer;
  background: #000;
  border: none;
  border-radius: 4px;
  margin-right: .5rem;
  padding: .4rem .8rem;
}

.creat-post {
  padding-bottom: 10px;
}

.posts-title {
  color: #000;
  font-size: 25px;
}
